const storage = {
    fetch() {
        const arr = [];
        if(localStorage.length > 0) {
            for(let i = 0; i < localStorage.length ; i++) {
                if(localStorage.key(i) !== 'loglevel:webpack-dev-server')
                    arr.push(JSON.parse(localStorage.getItem(localStorage.key(i))));
                    // console.log(localStorage.key(i));
            }
        }
        return arr;
    }
}

const state = {
    todoItems: storage.fetch()
};

const getters = {
    storedTodoItem(state) {
        return state.todoItems;
    }
}

const mutations = {
    addOwnItem(state, todoItem) {
        const obj = {compleated: false, item:todoItem}
        localStorage.setItem(todoItem, JSON.stringify(obj));
        state.todoItems.push(obj);
    },
    removeOwnIrem(state, payload) {
        const {index, todoItem} = payload;
        localStorage.removeItem(todoItem.item);
        state.todoItems.splice(index, 1);
    },
    toggleOwnItem(state, payload) {
        const {index, todoItem} = payload;
        state.todoItems[index].compleated = !state.todoItems[index].compleated;
        // 로컬 스토리지 갱신
        localStorage.removeItem(todoItem.item);
        localStorage.setItem(todoItem.item, JSON.stringify(todoItem));
    },
    crearAllItem(state) {
        localStorage.clear();
        state.todoItems = [];
    }
}

export default {
    state,
    getters,
    mutations
}